<template>
  <div class="hello">
    <br/> 
    <div class="ui grid">
      <div class="ui two column row">
        <div class="ui segment column">
          <h3 class="ui header"></h3>
          <div class="ui large bulleted list">
            <div class="item" v-for="n in news.slice(0,3)">
              <span v-html="n"></span>
            </div>
          </div>
        </div>
        <div class="ui segment column">
          <h3 class="ui header"></h3>
          <div class="ui large ordered list">
            <div class="item">
              
            </div>
            <div class="item">
              ()
            </div>
            <div class="item">
              GOOGLE
            </div>
            <div class="item">
              <a href="https://www.facebook.com/groups/156709241062806/" target="_blank">
                <i class="facebook icon" />
              </a>
            </div>
          </div>
        </div><!-- Segment END -->
      </div>
    </div>
    <div class = "ui container">
      <div class="ui input">
        <input id="find" type="text" v-model="s" placeholder="" autofocus="">
      </div>
      <router-link class = 'item' to='/stones' exact=''>
        <i class="map icon" />
      </router-link>
      <div class="ui stackable grid">
        <div class="ui ten wide column">
          <table class="ui celled striped table">
            <tr><th colspan="4">
              
            </th>
            <tr><th></th><th></th><th>    
            <router-link class = 'item ' to='/donate' exact=''>
              <i class="gift icon" />
            </router-link></th></tr>
            <tr class="item" v-for="(u, index) in units" v-show="!s || u.n.indexOf(s) > -1 || (s >= u.g && s <= u.G)">
              <td>
                <a target="_blank" :href="u.url || ('https://3dw.github.io/mathprint/'+u.n+'.doc')">
                  <img :src="'https://www.google.com/s2/favicons?domain='+u.url" />{{ u.n }}(
                  <span v-show="u.g <= 0 && u.G <=0"></span>
                  <span v-show="u.g <= 0 && u.G > 0">~{{ u.G }}</span>
                  <span v-show="u.g > 0 && u.G > 0">{{ u.g }}~{{ u.G }}</span>)</a>
              </td>
              <td>            
                <a target="_blank" :href="u.url || ('https://3dw.github.io/mathprint/'+u.n+'.doc')">
                  <span v-show="u.edit"><i class="edit icon"></i>()</span>
                  <span v-show="!u.edit"><i class="comments icon"></i>()</span>
                </a>
              </td>
              <td>
                <a target="_blank" :href="u.url || ('https://3dw.github.io/mathprint/'+u.n+'.doc')">
             <!--     {{(u.p || (u.edit && 50) || 100) + '%'}} -->
                  <sui-progress :state="u.edit ? 'warning' : 'success'" :percent="u.p || (u.edit && 50) || 100" :label="(u.p || (u.edit && 50) || 100) + '%'" />
                </a>
              </td>
            </tr>
          </table>
          </div>
          <div class="ui six wide column">
              <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSeYKTrcBFtsT0QV0NE5oog624LDffR1AQsxB6Gf9lEY9O9LIg/viewform?embedded=true" width="320" height="1775" frameborder="0" marginheight="0" marginwidth="0">Loading...</iframe>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'hello',
  props: ['units'],
  data () {
    return {
      s: '',
      news: [
        '2019.05<a href="https://docs.google.com/document/d/14j0lEEZH5A1FNum7L6p9U1ETWx_0JTyasahIi9DLe3w/edit?usp=sharing" target="_blank"></a>',
        '2018.10.',
        '2018.10.',
        '2018.10.<a href="https://drive.google.com/drive/folders/0B-3AeNXITt7ZaExCaDkxNlZxUXc" target="_blank">GOOGLE</a>, GOOGLE DOC']
    }
  }
}
</script>

